@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Roboto', sans-serif;
  font-size: 62.5%; /* 10 / 16 = .625 */
  line-height: 1.2;
}

a {
  color: #17a2b8;
  color: var(--primary-color);
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

.btn {
  display: inline-block;
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
  padding: 1rem 2rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.btn:hover {
  opacity: 0.8;
}

.btn-primary {
  background: #17a2b8;
  background: var(--primary-color);
  color: #fff;
}

.btn-light {
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}

.btn-dark {
  background: #343a40;
  background: var(--dark-color);
  color: #fff;
}

.btn-danger {
  background: #dc3545;
  background: var(--danger-color);
  color: #fff;
}

.btn-success {
  background: #28a745;
  background: var(--success-color);
  color: #fff;
}

.btn-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
}

input {
  font-family: inherit;
  border: 1px solid #777;
  border-radius: 4px;
  outline: none;
  padding: 0.8rem 1.2rem;
  box-sizing: border-box;
}

input.invalid {
  border-color: #dc3545;
  border-color: var(--danger-color);
}

input:focus {
  border-color: #000;
}

i {
  font-weight: 400;
}

h1,
.h1 {
  font-weight: 400;
  font-size: 2.6rem;
  letter-spacing: 1.2px;
}

h2,
.h2 {
  font-weight: 400;
  font-size: 2.2rem;
  letter-spacing: 1.1px;
}

h3,
.h3 {
  font-weight: 300;
  font-size: 1.8rem;
}

hr,
.hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(#343a40, 0),
    rgba(#343a40, 0.75),
    rgba(#343a40, 0)
  );
  background-image: linear-gradient(
    to right,
    rgba(var(--dark-color), 0),
    rgba(var(--dark-color), 0.75),
    rgba(var(--dark-color), 0)
  );
}

.form-group {
  margin-bottom: 1.4rem;
}

.form-group label {
  display: block;
  font-size: 1.6rem;
  margin-bottom: 0.5rem;
}

.info {
  color: #343a40;
  color: var(--dark-color);
  font-size: 1.4rem;
}

.mg-sm {
  margin-bottom: 1rem;
}
.mg-md {
  margin-bottom: 2rem;
}
.mg-lg {
  margin-bottom: 3rem;
}
.mg-hg {
  margin-bottom: 6rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  letter-spacing: 2px;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #17a2b8;
  color: var(--primary-color);
}

.text-dark {
  color: #343a40;
  color: var(--dark-color);
}

.form-group .editor-tool-bar {
  border: 1px solid #ccc;
}

.form-group .editor-content {
  border: 1px solid #ccc;
  min-height: 30rem;
}

.form-group .editor-content:focus {
  border: 1px solid #000;
  padding: 0 8px;
  min-height: 30rem;
}

.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1rem 2rem;
  background-color: var(--primary-color);
  color: var(--light-color);
}

.navbar ul {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.navbar li {
  display: inline-block;
  font-size: 1.8rem;
}

.navbar li a {
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: all 0.25s;
}

.navbar li a.active {
  color: var(--primary-color);
  background-color: var(--light-color);
}

.navbar li:not(:last-child) {
  margin-right: 1.8rem;
}

.navbar a {
  color: var(--light-color);
}

.container {
  margin-top: 2rem;
}

.login-form {
  max-width: 40rem;
  margin: auto;
  margin-bottom: 1rem;

  border: 1px solid #ccc;
  padding: 2rem;
  border-radius: 4px;
}

.login-form input,
.login-form button[type='submit'] {
  width: 100%;
}

.register-form {
